import React, { memo } from 'react'
import Item from './item';
import ItemVoice from '../../assets/img/item_voice.png';
import ItemSetting from '../../assets/img/item_setting.png';
import ItemChair from '../../assets/img/item_chair.png';
import ItemTime from '../../assets/img/item_time.png';
import ItemFile from '../../assets/img/item_file.png';
import ItemApp from '../../assets/img/item_app.png';
import ItemFollow from '../../assets/img/item_follow.png';
import ItemAdd from '../../assets/img/item_add.png';

import { ContainerWrapper, ItemWrapper } from './style';

export default memo(function Container() {
  return (
    <ContainerWrapper>
      <ItemWrapper>
        <Item ItemPic={ItemVoice} ItemText="语音交互" />
        <Item ItemPic={ItemSetting} ItemText="设置" />
        <Item ItemPic={ItemChair} ItemText="预约座位" />
      </ItemWrapper>
      <ItemWrapper>
        <Item ItemPic={ItemTime} ItemText="时间" />
        <Item ItemPic={ItemFile} ItemText="文件管理" />
        <Item ItemPic={ItemApp} ItemText="我的应用" />
      </ItemWrapper>
      <ItemWrapper>
        <Item ItemPic={ItemFollow} ItemText="跟随" />
        <Item ItemPic={ItemAdd} ItemText="添加" />
        <Item className="itemNone" ItemPic={ItemVoice} ItemText="9" />
      </ItemWrapper>
    </ContainerWrapper>
  )
})
